import { useState } from 'react';
import { Link } from 'react-router-dom';
import '../styles/components/navBar.css';
import Logo from '../images/Logo.svg';

function NavBar() {
  const [menu, setMenu] = useState(true);
  const menuSection = document.querySelector(".menu-section");

  function openMenu() {
    if (menu === true) {
      document.body.style.overflow = "initial";
      menuSection?.classList.remove("on");
      setMenu(false);
    } if (menu === false) {
      document.body.style.overflow = "hidden";
      menuSection?.classList.add("on");
      setMenu(true);
    }
  }

  return (
    <div className="navBarTemplate">
      <header>
        <div className="container">
          <img
            src={Logo}
            alt="Rocketseat"
          />
          <div className="menu-section">
            <Link to="#" onClick={openMenu}>
              <div className="menu-toggle">
                <div className="one"></div>
                <div className="two"></div>
                <div className="three"></div>
              </div>
            </Link>
            <nav>
              <ul>
                <li><Link to="/" onClick={openMenu}>Home</Link></li>
                <li><Link to="/sobre" onClick={openMenu}>Sobre</Link></li>
                <li><Link to="/competencias" onClick={openMenu}>Competências</Link></li>
                <li><Link to="/portfolio" onClick={openMenu}>Portfólio</Link></li>
                <li><Link to="/contato" onClick={openMenu}>Contato</Link></li>
              </ul>
            </nav>
          </div>
        </div>
      </header>
    </div>
  );
}



export default NavBar;