import '../styles/pages/sobre.css';
import Perfil from '../images/Perfil.jpg';

function Sobre(){
    return (
        <div className="sobreTemplate">
            <div className="col-2">
            <img src={Perfil} />
            </div>
            <div className="col-2">
            <ul>
                <li><p>Meu nome é Leonardo Lucca Oliveira Branco Rocha de Moraes.</p></li>
                <li><p>Tenho 21 anos.</p></li>
                <li><p>Nascido e criado na cidade de Guaratinguetá, estado de São Paulo.</p></li>
                <li><p>Atualmente estudando Engenharia da Computação no Centro Universitário Salesiano de São Paulo, Lorena</p></li>
                <li><p>Sempre buscando novos desafios que possam ajudar em meu crescimento e aprendizado, acompanhando tudo isso com uma boa xícara de café.</p></li>
            </ul>
            </div>
        </div>
    );
}

export default Sobre;